import ExerciseAppbar from "../ExerciseAppbar.js";
import Headline from "../Headline";
import BackButton from "../BackButton";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import React, { useState, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import Container from "@mui/material/Container";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import Box from "@mui/material/Box";
import Slider from "@mui/material/Slider";
import Stack from "@mui/material/Stack";
import TextField from "@mui/material/TextField";
import StepperExercise from "../StepperExcercise";
import ProgressTracker from "../utility/ProgressTracker";
import { Fade } from "@mui/material";
import useMediaQuery from "@mui/material/useMediaQuery";
import stepsArrays from "../utility/stepperArrays";

function LifeAreasPriority() {
  const [lifeAreasState, setLifeAreas] = React.useState([]);
  const [today, setToday] = React.useState();
  const [todayError, setTodayError] = React.useState(false);
  const [howImportent, setHowImportent] = React.useState();
  const [howImportentError, setHowImportentError] = React.useState(false);
  const [whyImportent, setWhyImportent] = React.useState("");
  const [lifeAreasDone, setLifeAreasDone] = React.useState(0);
  const [isItDone, setIsItDone] = React.useState(false);
  const [showLifeArea, setShowLifeArea] = React.useState(0);

  const steps = stepsArrays.lifeAreas;

  const howHappyLable = [
    "Helt och hllet missnjd",
    "Mycket missnjd",
    "Missnjd",
    "Ganska missnjd",
    "Lite missnjd",
    "Varken njd eller missnjd",
    "Lite njd",
    "Ganska njd",
    "Njd",
    "Mycket njd",
    "Helt och hllet njd",
  ];

  const howImportentLable = [
    "Helt och hllet oviktigt",
    "Mycket oviktigt",
    "Oviktigt",
    "Ganska oviktigt",
    "Lite oviktigt",
    "Varken viktigt eller oviktigt",
    "Lite viktigt",
    "Ganska viktigt",
    "Viktigt",
    "Mycket viktigt",
    "S viktigt det kan bli",
  ];

  let saveAs = "prioLifeAreas";

  const smallScreen = useMediaQuery("(max-width:445px)");

  useEffect(() => {
    if (showLifeArea === lifeAreas.length) {
      setIsItDone(true);
    }
  }, [showLifeArea]);

  function valuetext(value) {
    return "${value}";
  }
  const history = useHistory();

  const nextPage = () => {
    localStorage.setItem(saveAs, JSON.stringify(lifeAreasState));

    history.push({
      pathname: "/livsomraden-varderingar",
      state: lifeAreasState,
    });
  };

  const location = useLocation();
  const localLifeAreas = localStorage.getItem("lifeAreas");
  const lifeAreas = location.state
    ? location.state
    : JSON.parse(localLifeAreas);
  // location.state;
  console.log(lifeAreas);

  const nextLifeArea = () => {
    console.log("hehe");
  };
  const resetValues = () => {
    setToday();
    setHowImportent();
  };

  const handleSubmit = ({ title }) => {
    window.scrollTo(200, 0);
    if (today === undefined && howImportent === undefined) {
      setTodayError(true);
      setHowImportentError(true);
      return;
    }
    if (today === undefined) {
      console.log("noToday");
      setTodayError(true);
      return;
    }
    if (howImportent === undefined) {
      console.log("noImportente");
      setHowImportentError(true);
      return;
    }
    // det objektet som ska sparas
    let lifeArea = {
      title: title,
      today: today,
      howImportent: howImportent,
      whyImportent: whyImportent,
      diff: howImportent - today,
    };
    console.log("---------------------");
    console.log(lifeAreasState);
    // ((prevValues) => [...prevValues, lifeArea]) stter in den nya "lifeArea" sist i lifeAres arrayn
    setLifeAreas((prevValues) => [...prevValues, lifeArea]);
    setShowLifeArea(showLifeArea + 1);
    resetValues();
  };

  useEffect(() => {
    if (showLifeArea === lifeAreas.length) {
      setIsItDone(true);
    }
  }, [showLifeArea]);

  useEffect(() => {
    setHowImportentError(false);
  }, [howImportent]);

  useEffect(() => {
    setTodayError(false);
  }, [today]);

  const goBackOneCard = () => {
    console.log("backar");
  };

  return (
    <div>
      <ExerciseAppbar header={"Dina Livsomrden"} step={" "} />
      <ProgressTracker
        nrsToPick={lifeAreas.length}
        nrsPicked={showLifeArea}
        isItDone={isItDone}
      />
      <Container>
        <StepperExercise activeStep={1} steps={steps} />

        {isItDone ? (
          <Headline text="Bra jobbat!" />
        ) : (
          <Headline text="Prioritera dina livsomrden" />
        )}

        <Box sx={{ maxWidth: "725px", mx: "auto" }}>
          {isItDone ? (
            <Typography>
              {" "}
              Nu r det bara ett steg kvar p denna vningen, tryck p nsta nr
              du r redo.
            </Typography>
          ) : (
            <Typography>
              Hr fr du jobba med att prioritera dina vrderingar utifrn hur
              viktiga dom r fr dig och hur du upplever att du har det inom
              omrdet idag. Tanken r att det ska hjlpa dig att se om du lgger
              din tid och energi p rtt stllen, gr du den vg som r
              trevligast fr dig eller har du fastnat i en rutin dr du gr
              mycket av sdant som inte tillfr ditt liv ngot vrde?
            </Typography>
          )}
        </Box>
        <Box>
          {lifeAreas.map(({ title }, index) => {
            // console.log(title);
            return (
              <Fade
                timeout={{ enter: 1200, exit: 1200 }}
                in={index === showLifeArea}
              >
                <Box
                  className={index === showLifeArea ? "simpelShow" : "hidden"}
                  boxShadow={10}
                  key={index}
                  sx={{
                    display: "table",
                    border: "1px solid lightgrey",
                    margin: "15px",
                    borderRadius: "6px",
                    mx: "auto",
                    mb: "50px",
                    width: "100%",
                    maxWidth: "900px",
                    mt: "50px",
                    "&:hover": {
                      transform: "scale(1.01)",
                    },
                  }}
                >
                  <Box
                    boxShadow={0}
                    sx={{
                      color: "white",
                      padding: "15px",
                      pt: "20px",
                      pb: "20px",
                      borderRadius: "6px 6px 0px 0px",
                      position: "relative",
                    }}
                  >
                    <Box className="inverted-border-radius-left"> </Box>
                    <Box className="inverted-border-radius-right"> </Box>

                    <Typography variant="h2" textAlign={"center"} sx={{}}>
                      {" "}
                      {title}
                    </Typography>
                  </Box>
                  <form onSubmit={handleSubmit}>
                    <Box
                      className={todayError && "errorBorder"}
                      sx={{
                        bgcolor: "primary.extraLight",
                        padding: "15px",
                        pt: "30px",
                        pb: "30px",
                        borderRadius: "0px 0px 0px 0px",
                      }}
                    >
                      <Typography
                        variant="h4"
                        textAlign={"center"}
                        sx={{ mb: "10px" }}
                      >
                        Hur njd r du med livsomrdet "{title}" idag?
                      </Typography>
                      {!todayError ? (
                        <Typography
                          variant="body1"
                          textAlign={"center"}
                          sx={{ mb: "10px" }}
                        >
                          - {howHappyLable[today]}{" "}
                        </Typography>
                      ) : (
                        <Typography
                          variant="body1"
                          textAlign={"center"}
                          sx={{ mb: "10px", color: "error.main" }}
                        >
                          Du mste svara p hur njd du r med detta omrde idag
                          fr att g vidare
                        </Typography>
                      )}

                      <Stack
                        spacing={2}
                        direction="row"
                        sx={{}}
                        justifyContent="center"
                      >
                        <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                          0
                        </Typography>
                        <Slider
                          aria-label={title}
                          defaultValue={5}
                          getAriaValueText={valuetext}
                          valueLabelDisplay="auto"
                          step={1}
                          min={0}
                          max={10}
                          sx={{ width: "75%" }}
                          onChange={(e) => setToday(e.target.value)}
                        />
                        <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                          10
                        </Typography>
                      </Stack>
                    </Box>

                    <Box
                      className={howImportentError && "errorBorder"}
                      sx={{ padding: "15px", pt: "30px", pb: "30px" }}
                    >
                      <Typography
                        variant="h4"
                        textAlign={"center"}
                        sx={{ mb: "10px" }}
                      >
                        Hur viktigt r livsomrdet "{title}" fr dig?
                      </Typography>

                      {!howImportentError ? (
                        <Typography
                          variant="body1"
                          textAlign={"center"}
                          sx={{ mb: "10px" }}
                        >
                          - {howImportentLable[howImportent]}
                        </Typography>
                      ) : (
                        <Typography
                          variant="body1"
                          textAlign={"center"}
                          sx={{ mb: "10px", color: "error.main" }}
                        >
                          Du mste vlja hur viktigt detta omrde r fr att g
                          vidare
                        </Typography>
                      )}

                      <Stack
                        spacing={2}
                        direction="row"
                        sx={{ mb: 1 }}
                        justifyContent="center"
                      >
                        <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                          {" "}
                          0{" "}
                        </Typography>{" "}
                        <Slider
                          aria-label={title}
                          defaultValue={5}
                          getAriaValueText={valuetext}
                          valueLabelDisplay="auto"
                          step={1}
                          min={0}
                          max={10}
                          sx={{ width: "75%" }}
                          onChange={(e) => setHowImportent(e.target.value)}
                        />
                        <Typography sx={{ fontWeight: "bold", pt: "3px" }}>
                          {" "}
                          10{" "}
                        </Typography>
                      </Stack>
                    </Box>

                    <Box
                      sx={{
                        bgcolor: "primary.extraLight",
                        padding: "25px",
                        pt: "30px",
                        pb: "60px",
                      }}
                    >
                      <Typography
                        variant="h4"
                        textAlign={"center"}
                        sx={{ mb: "15px" }}
                      >
                        Skriv kort om varfr omrdet r viktigt fr dig.
                      </Typography>
                      <TextField
                        fullWidth
                        id="obstacle"
                        label="Det r viktigt fr mig fr att...."
                        variant="outlined"
                        sx={{ backgroundColor: "white" }}
                        onChange={(e) => setWhyImportent(e.target.value)}
                      />

                      <Button
                        variant="contained"
                        aria-label="Backa"
                        startIcon={<ArrowBackIosIcon />}
                        onClick={() => goBackOneCard()}
                        sx={{
                          position: "absolute",
                          padding: "10px",
                          borderRadius: " 0  6px 0 6px",
                          left: "0px",
                          bottom: "0px",
                        }}
                      >
                        {smallScreen ? "Backa" : "Frra livsomrdet"}
                      </Button>

                      <Button
                        variant="contained"
                        aria-label="Backa"
                        endIcon={<ArrowForwardIosIcon />}
                        onClick={() => handleSubmit({ title: title })}
                        sx={{
                          position: "absolute",
                          padding: "10px",
                          borderRadius: "6px 0  6px 0",
                          right: "0px",
                          bottom: "0px",
                        }}
                      >
                        {smallScreen ? "Nsta" : "Nsta livsomrde"}
                      </Button>
                    </Box>
                  </form>
                </Box>
              </Fade>
            );
          })}
        </Box>
        <BackButton goTo={"/valj-livsomraden"}>Tillbaka</BackButton>
        <Button
          variant="contained"
          disabled={!isItDone}
          color="success"
          aria-label="Nsta"
          endIcon={<ArrowForwardIosIcon />}
          onClick={nextPage}
          sx={{ float: "right", mb: "15px", mt: "45px", mr: "15px" }}
        >
          Nsta
        </Button>
        <Box sx={{ width: "100%", height: "15px", clear: "both" }}> </Box>
      </Container>
    </div>
  );
}

export default LifeAreasPriority;
