import React, { useState, useEffect } from "react";
import ValuePicker from "../ValuePicker";
import lifeAreas from "./lifeAreas";
import { Typography } from "@mui/material";
import ExerciseAppbar from "../ExerciseAppbar";
import Container from "@mui/material/Container";
import Headline from "../Headline";
import { useLocation } from "react-router-dom";
import StepperExercise from "../StepperExcercise";
import Box from "@mui/material/Box";
import Fade from "@mui/material/Fade";
import stepsArrays from "../utility/stepperArrays";

function LifeareasPicker() {
  const location = useLocation();
  const valueArray = location.state;
  console.log(valueArray);

  const steps = stepsArrays.lifeAreas;

  const [slide, setSlide] = useState(false);

  useEffect(() => {
    setSlide(true);
  }, []); // Only re-run the effect if count changes
  // useEffect krs p varje render, hr hmtas data frn JSON server

  return (
    <div>
      <div>
        <ExerciseAppbar header={"Livsomrden"} step={""} />
        <StepperExercise activeStep={0} steps={steps} />
        <Fade in={slide} mountOnEnter unmountOnExit>
          <Container sx={{ backgroundColor: "white" }}>
            <Headline text="Vlj dina livsomrden" />
            <Box sx={{ maxWidth: "725px", mx: "auto" }}>
              <Typography variant="body1">
                {/* finns x antal ord ls igenom alla innan du gr vidare 
                nr du r klar: sker att du vill g vidare (har du lst alla?) */}
                Nedan ser du en lista p ngra viktiga livsomrden. Ls igenom
                listan och fundera ver vilka fem av dessa omrden som r
                viktigast fr dig. Du vljer sjlv hur du avgr vilka som r
                viktiga; du kan utg frn vilka omrden du upplever ett stort
                behov av frndring inom eller omrden som helt enkelt r av
                strst betydelse fr dig.
              </Typography>

              <br></br>
              {/* om values finns s krs ValuesComponet  */}

              {lifeAreas && (
                <ValuePicker
                  values={lifeAreas}
                  nrsToPick={3}
                  type="livsomrden"
                  next="/livsomraden-prioriteringar"
                  back="/livsomraden-information"
                  saveAs="lifeAreas"
                />
              )}
            </Box>
          </Container>
        </Fade>
      </div>
    </div>
  );
}

export default LifeareasPicker;
